@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
.card{position:relative;background:none;box-shadow:0 3px 5px -2px #424242;border-radius:8px;border:1px solid #424242}.card:hover{box-shadow:0 4px 5px -2px #424242;-webkit-transform:scale(101%);transform:scale(101%)}.card img{border-radius:8px;width:90px;height:140px;display:block}@media screen and (max-width: 400px){.card img{width:20vw;height:30vw}}.card .front{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition:-webkit-transform ease-in .2s;transition:transform ease-in .2s;transition:transform ease-in .2s, -webkit-transform ease-in .2s;position:absolute}.card .back{cursor:pointer;transition:-webkit-transform ease-in .2s;transition:transform ease-in .2s;transition:transform ease-in .2s, -webkit-transform ease-in .2s;transition-delay:.2s}.flipped{box-shadow:0 3px 5px -2px #424242}.flipped .front{-webkit-transform:rotateY(0deg);transform:rotateY(0deg);transition-delay:.2s}.flipped .back{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition-delay:0s}

.App{margin:auto;height:100vh;width:100%;text-align:center;position:relative}header{height:15%;width:100%;display:grid;place-items:center;background:#141414;box-shadow:0 8px 7px -4px #404040}header h4{margin:0 0 1rem 0}.main{height:70%;padding:auto;display:grid;place-items:center}.main .cards{max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;display:grid;place-items:center;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:15px;gap:15px}@media screen and (max-width: 380px){.main .cards{grid-template-columns:1fr 1fr 1fr}}footer{height:15%}footer span{display:inline;align-items:center;grid-template-columns:1fr 1fr;max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content}footer span p{font-size:30px;display:inline}footer span .steps{color:yellow;-webkit-animation:steps 1s ease-in-out infinite alternate;animation:steps 1s ease-in-out infinite alternate}footer .new-game{padding:1rem}footer .new-game button{display:inline-block;background:#124fc0;box-shadow:0 0 15px -5px #838383;transition:scale .3s;padding:5px 10px;margin:0;border-radius:5px;color:#fff;cursor:pointer;outline:none;border:none}footer .new-game button:hover{background:#0e3c91;box-shadow:0 4px 9px -4px #838383}footer .new-game button:active{-webkit-transform:scale(97%);transform:scale(97%)}@-webkit-keyframes steps{from{opacity:0}to{opacity:1}}@keyframes steps{from{opacity:0}to{opacity:1}}

*, *::before, *::after { box-sizing: border-box; }

body {
    height: 100vh;
    background: #0a0a0a;
    font-family: 'Nunito', sans-serif;
    margin: 0;
    text-align: center;
    color: #fff;
}
